import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Ng2TableModule } from 'ng2-table/ng2-table';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { NgxPaginationModule } from 'ngx-pagination';
import { SharedModule } from '../../shared/shared.module';
import { CommonPopUpModule } from '../commonPopUp/commonPopUp.module';
import { CommonModule } from '@angular/common';
import { Ng2Rut } from 'ng2-rut';

import { ConsultarPedidosComponent } from './consultarPedidos/consultarPedidos.component';
import { CrearPedidoComponent } from './crearPedido/crearPedido.component';
import { ZonasTodasPopUpComponent } from '../commonPopUp/zonasTodasPopUp/zonasTodasPopUp.component';
import { RutAsociadoPopUpComponent } from '../commonPopUp/rutAsociadoPopUp/rutAsociadoPopUp.component';
import { ListaLocalesPopUpComponent } from '../commonPopUp/listaLocalesPopUp/listaLocalesPopUp.component';
import { RelaComerPedidoPopUpComponent } from '../commonPopUp/relaComerPedidoPopUp/relaComerPedidoPopUp.component';
import { SucursalesPopUpComponent } from '../commonPopUp/sucursalesPopUp/sucursalesPopUp.component';
import { RutasPopUpComponent } from '../commonPopUp/rutasPopUp/rutasPopUp.component';
import { CierreProcesoComponent } from './cierreProceso/cierreProceso.component';
import { ValidacionUsuarioPedidoPopUpComponent } from '../commonPopUp/validacionUsuarioPedidoPopUp/validacionUsuarioPedidoPopUp.component';
import { GestionRecorridosComponent } from './gestionRecorridos/gestionRecorridos.component';
import { ConfirmacionRecorridosComponent } from './confirmacionRecorridos/confirmacionRecorridos.component';
import { UtilGeoPopUpComponent } from '../commonPopUp/utilGeoPopUp/utilGeoPopUp.component';
import { GestionRecorridoPopUpComponent } from '../commonPopUp/gestionRecorridoPopUp/gestionRecorridoPopUp.component';
import { TransportistasPopUpComponent } from '../commonPopUp/transportistasPopUp/transportistasPopUp.component';
import { ActualizaclientepopupComponent } from '../commonPopUp/actualizaclientepopup/actualizaclientepopup.component';

const routes: Routes = [
  { path: 'crearPedido', component: CrearPedidoComponent},
  { path: 'consultarPedidos', component: ConsultarPedidosComponent},
  { path: 'cierreProceso', component: CierreProcesoComponent},
  { path: 'gestionRecorridos', component: GestionRecorridosComponent},
  { path: 'confirmaRecorridos', component: ConfirmacionRecorridosComponent}
];

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forChild(routes),
    Ng2TableModule,
    NgxDatatableModule,
    NgxPaginationModule,
    CommonPopUpModule,
    Ng2Rut
  ],
  declarations: [ConsultarPedidosComponent,
    CrearPedidoComponent,
    CierreProcesoComponent,
    GestionRecorridosComponent,
    ConfirmacionRecorridosComponent
  ],
  entryComponents: [
    ZonasTodasPopUpComponent,
    RutAsociadoPopUpComponent,
    ListaLocalesPopUpComponent,
    RelaComerPedidoPopUpComponent,
    SucursalesPopUpComponent,
    RutasPopUpComponent,
    ValidacionUsuarioPedidoPopUpComponent,
    UtilGeoPopUpComponent,
    GestionRecorridoPopUpComponent,
    TransportistasPopUpComponent,
    ActualizaclientepopupComponent
  ],
  exports: [
    RouterModule
  ]
})
export class PedidosModule { }
