import { Component, OnInit, OnDestroy } from '@angular/core';
import * as _ from 'lodash';
import { HttpClient } from '@angular/common/http';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilService } from './../../../services/util.service';
import { PedidosService } from '../../../core/consultaService/pedidos.service';
import { ValidacionUsuarioPedidoPopUpComponent } from '../../commonPopUp/validacionUsuarioPedidoPopUp/validacionUsuarioPedidoPopUp.component';

declare var jQuery: any;
declare var $: any;
let swal = require('sweetalert');
@Component({
  selector: 'app-cierreProceso',
  templateUrl: './cierreProceso.component.html',
  styleUrls: ['./cierreProceso.component.scss']
})
export class CierreProcesoComponent implements OnInit, OnDestroy {
  forma: FormGroup;
  request: any = {};
  user: any = {};
  datos: Array <any> = [];
  validacionModal: BsModalRef;
  origen: string;
  titulo: string;
  subData: any;


  constructor(fb: FormBuilder, private modalService: BsModalService, private authService: AuthService,
    public loader: NgxSpinnerService, private router: Router, private utilService: UtilService,
    private pedidosService: PedidosService) {
      this.forma = new FormGroup ({
        fpr_dfhoproceso: new FormControl({value: '', disabled: true })
      });
     }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.subData = this.pedidosService.datos$.subscribe(resData => {
      this.setData(resData);
    });
    this.fechaActualProceso();
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }
  fechaActualProceso() {
    this.request.nRutOperador = Number(this.user.nrutoperador);
    this.request.sCodProceso = 'O2C';
    this.pedidosService.getFechaProceso(this.request)
          .subscribe((data: any) => {
            this.loader.hide();
            if (data) {
              if (data.error.codError !== 0) {
                  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                } else {
                  this.datos = data.fechaProceso;
                  if (this.datos.length > 0) {
                    console.log(this.datos);
                    this.forma.controls.fpr_dfhoproceso.setValue(this.utilService.dateStringServiceToStringFormat(
                      this.datos[0].fpr_dfhoproceso));
                      console.log(this.forma.controls.fpr_dfhoproceso.value);
                  } else {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                  }
                }
            }
          },
          error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });
  }
  cierraProceso() {
    this.origen = 'validacionProceso';
    let initialState = {
      titulo: 'Validación de Usuario',
      origen: 'validacionProceso'
    };
    this.validacionModal = this.modalService.show(ValidacionUsuarioPedidoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  setData(resData) {
    console.log(resData);
    if (resData !== 0) {
      switch (this.origen) {
        case 'validacionProceso': {
          console.log('CERRO');

          this.fechaActualProceso();
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }

}
